@font-face {
  font-family: 'URWDIN-Light';
  src: local('URWDIN-Light'), url(./assets/fonts/URWDIN/URWDIN-Light.otf) format('truetype');
}
@font-face {
  font-family: 'URWDIN-Black';
  src: local('URWDIN-Black'), url(./assets/fonts/URWDIN/URWDIN-Black.otf) format('truetype');
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
body {
  font-family: 'URWDIN-Light', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body::-webkit-scrollbar {
  display: none;
}

#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

p {
  margin: 2px;
}

ul {
  margin: 0;
  padding: 0;
}
